@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400;700;900&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
::-webkit-scrollbar {
  width: 4px;
}
::-webkit-scrollbar-track {
  background: #181818;
}
::-webkit-scrollbar-thumb {
  background: rgb(44, 44, 44);
}
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

*{
  box-sizing: border-box;
  font-family: 'Source Sans Pro', sans-serif;
}
body{
  background-color: rgb(24, 24, 24);
  color: white;
  height: 100vh;
  overflow: hidden;
}
.app-wrapper{
  height: 100vh;
  display: flex;
  flex-direction: column;
}
.games-wrapper{
  display: flex;
  flex-grow: 1;
  flex-wrap: wrap;
  position: relative;
  overflow-y: scroll;
  height: 100%;
  max-width: 1320px;
  margin: auto;
}
.game-card-wrapper{
  flex-basis: 20%;
  flex-grow: 0;
  flex-shrink: 0;
}
.headerMainWrapper{
    display: flex;
    flex-basis: 20px;
    background-color: #181818;
    min-height: 60px;
    align-items: center;
    justify-content: center;
}
.headerWrapper{
    display: flex;
    align-items:center;
    max-width: 1320px;
    margin: auto;
    width: 100%;
}
.searchBarWrapper{
    position: relative;
    flex-grow:1;
}
.searchBarWrapper > .searchBar{
    background-color:#181818;
    border:3px solid #1f1f1f;
    min-height:40px;
    border-radius:5px;
    padding-left:20px;
    color: white;
    width: 100%;
}
.searchBarWrapper > .searchBar::-webkit-input-placeholder{
    letter-spacing: 1px;
}
.searchBarWrapper > .searchBar:-ms-input-placeholder{
    letter-spacing: 1px;
}
.searchBarWrapper > .searchBar::placeholder{
    letter-spacing: 1px;
}
.logo{
    width: 70px;
    height: 50px;
    margin-right:50px;
}
.searchBarWrapper > .searchLoadingIcon{
    width:20px;
    height:20px;
    position: absolute;
    right: 10px;
    top: 10px;
}
.game-card{
    max-width: 200px;
    margin: 20px auto;
    padding: 10px 10px;
    display: block;
    color: white;
    text-decoration: none;
}
.game-card .platform-wrapper > img{
    width: 12px;
    height: 12px;
    margin-right: 7px;
    margin-top: 7px;
    opacity: .2;
}
.game-card .star-wrapper > img{
    width: 10px;
    height: 10px;
    margin-right: 5px;
}

.game-cover{
    width: 100%;
    min-height: 220px;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
}
.game-cover > img{
    position: absolute;
    top: 0px;
    height: 100%;
    transform: translateX(-25%);
}
.game-name{
    font-size: 16px;
    margin-top: 8px;
    margin-bottom: 4px;
    font-weight: 100;
}
#game-page-wrapper .buy-now-section {
  text-transform: lowercase;
}
#game-page-wrapper .buy-now-section .buy-now-title {
  text-transform: uppercase;
  letter-spacing: 3px;
  font-weight: 700;
}
#game-page-wrapper .buy-now-section .buy-now-buttons-wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-top: 10px;
}
#game-page-wrapper .buy-now-section .buy-button {
  display: flex;
  align-items: center;
  background-color: white;
  border-radius: 5px;
  padding: 10px;
  margin-right: 10px;
  margin-bottom: 10px;
  text-decoration: none;
  text-transform: uppercase;
  color: white;
  font-weight: normal;
  letter-spacing: 2px;
  font-size: 14px;
}

#game-page-wrapper .buy-now-section .buy-button img {
  margin-right: 5px;
}

#game-page-wrapper {
  overflow-y: scroll;
}
#game-page-wrapper .cover-image {
  min-height: 250px;
  background-size: cover;
  background-position-x: center;
  background-position-y: 20%;
}
#game-page-wrapper .section-1 {
  display: flex;
  max-width: 1200px;
  margin: auto;
  padding-top: 20px;
}
#game-page-wrapper .section-1 > div {
  flex-grow: 1;
}
#game-page-wrapper .section-1 .buy-now-section {
  flex-basis: 350px;
  flex-grow: 0;
}

#game-page-wrapper .section-1 .game-name {
  font-weight: normal;
  text-transform: uppercase;
  font-size: 36px;
  letter-spacing: 2px;
}

#game-page-wrapper .section-1 .publisher {
  text-transform: uppercase;
  font-size: 11px;
  letter-spacing: 2px;
}
#game-page-wrapper .section-1 .rating-start-wrapper {
  margin-top: 20px;
}
#game-page-wrapper .section-1 .highest-rating-title {
  text-transform: lowercase;
  font-weight: normal;
  font-size: 14px;
  letter-spacing: 2px;
  margin-top: 5px;
  margin-bottom: 0px;
}
#game-page-wrapper .section-1 .rating {
  text-transform: lowercase;
  font-weight: 900;
  font-size: 26px;
  letter-spacing: 2px;
  margin-top: 0px;
}
#game-page-wrapper .detailed-content-wrapper {
  max-width: 1200px;
  margin: auto;
  padding-top: 20px;
  display: flex;
}
#game-page-wrapper .detailed-content-wrapper .main-contents{
  padding-right: 50px;
}
#game-page-wrapper .detailed-content-wrapper .sidebar-contents {
  flex-basis: 350px;
  min-width: 350px;
  flex-grow: 0;
}
#game-page-wrapper .detailed-content-wrapper .sidebar-contents .title,
#game-page-wrapper .detailed-content-wrapper .main-contents .title {
  font-size: 20px;
  letter-spacing: 2px;
  color: #464646;
  margin-bottom: 3px;
}
#game-page-wrapper .detailed-content-wrapper .description-wrapper .description {
  font-size: 14px;
  letter-spacing: 1px;
  color: #9b9b9b;
  display: block;
}
#game-page-wrapper .detailed-content-wrapper .ratings-wrapper .elements-wrapper {
  
}
#game-page-wrapper .detailed-content-wrapper .ratings-wrapper .elements-wrapper > .row{
  display: flex;
  justify-content: flex-end;
  width: 100%;
  margin-top: 10px;
}
#game-page-wrapper .detailed-content-wrapper .ratings-wrapper .elements-wrapper .row p{
  margin: 0px;
  color: #e2e2e2;
}
#game-page-wrapper .detailed-content-wrapper .ratings-wrapper .elements-wrapper .row p.rating-title{
  margin-right: auto;
  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: 3px;

}
#game-page-wrapper .detailed-content-wrapper .ratings-wrapper .elements-wrapper .row p.count{
  margin-right: 10px;
}
#game-page-wrapper .detailed-content-wrapper .ratings-wrapper .elements-wrapper .row p.percent{
  width:60px;
  text-align: right;
}
#game-page-wrapper .detailed-content-wrapper .ratings-wrapper .elements-wrapper .rating-bar{
  height: 2px;
  margin-top: 5px;
}

